import lFile from '@/components/l-file/l-file.vue'
export default {
	components: {
		lFile
	},
	data() {
		return {
			dataList: []
		}
	},
	onLoad() {
		this.loadData();
		this.$nextTick(function() {
			let input = document.createElement('input')
			input.type = 'file';
			input.style.position='absolute';
			input.style.left='0';
			input.style.right='0';
			input.style.bottom='0';
			input.style.top='0';
			input.style.opacity=0;
			this.$refs.input.$el.appendChild(input)
			input.onchange = (e) => {
				console.log(e.srcElement.value)
				uni.showLoading({
					title:'上传中....'
				});
				// console.log(this)
				uni.uploadFile({
					url: 'http://jiuyema.youkhui.cn/works/api/uploadImage',
					filePath:e.srcElement.value,
					name: 'file',
					method: "POST",
					headers: {
						'Content-Type': 'application/json',
						'Content-Type': 'multipart/form-data;'
					},
					formData:{
						file:e.target.files[0]
					},
					success: function(res) {
						uni.hideLoading();
						if(res.statusCode==200){
							uni.openDocument({
								filePath:res.filePath,
								success: function(res) {
									console.log('打开文档成功');
								}
							});
						}else if (res.statusCode== 500) {
							uni.showToast({
								title: "系统异常",
								icon: "none"
							});
						}else{
							uni.showToast({
								title: "未知错误",
								icon: "none"
							});
						}
					},
					fail: function(res) {
						uni.hideLoading();
						uni.showToast({
							title: "服务器无连接",
							icon: "none"
						});
					}
				});
			}
		})
	},
	methods: {
		downLoad(url) {
			const downloadTask = uni.downloadFile({
				url: url,
				success: (res) => {
					if (res.statusCode === 200) {
						uni.saveFile({
							filePath: res.tempFilePath,
							success(res) {
								uni.showToast({
									title: '保存成功',
									icon: "success",
									duration: 1000
								})
							}
						})
					}
					this.dd = res.tempFilePath;
					console.log(this.dd);
				}
			});
			downloadTask.onProgressUpdate((res) => {
				console.log('下载进度' + res.progress);
				console.log('已经下载的数据长度' + res.totalBytesWritten);
				console.log('预期需要下载的数据总长度' + res.totalBytesExpectedToWrite);
			});
		},
		loadData() {
			this._reqw.ipost({}, 'biographical').then(res => {
				res.result == 0 ? this.dataList = res.dataList : this.$api.tip(res.resultNote)
			}).catch(err => {})
		},
		upLoad() {
 
			// this.$api.navTo('/pages/webView')
		},
		upSuccess(e) {
